import React from 'react';

import { HBButtonSmall } from 'hbBaseClass';

import './Layout.scss';

interface Props {
  title?: string;
  btnBack?: string;
  onBack?: (param: any) => void;
  btnConfirm?: string;
  btnConfirmDisabled?: boolean;
  onConfirm?: () => void;
  children?: React.ReactNode;
}

const AppointmentDetailLayout: React.FC<Props> = (props) => {
  const { title = '', btnBack = 'Zurück', onBack = () => {}, children } = props;
  return (
    <div className="hb-mx-20 AppointmentDetailLayout-wrapper">
      <div className="AppointmentDetailLayout-header">
        {btnBack && (
          <div className="d-flex justify-content-between hb-my-28">
            <HBButtonSmall onClick={onBack}>
              <i className="hb-icon-arrow-left" /> {btnBack}
            </HBButtonSmall>
          </div>
        )}
      </div>
      <div className="AppointmentDetailLayout-wrapperContainer">
        {title && <h3 className="AppointmentDetailLayout-title">{title}</h3>}
        <div className={`AppointmentDetailLayout-container`}>{children}</div>
      </div>
    </div>
  );
};

export default AppointmentDetailLayout;
