import React from 'react';

import './AppointmentDetailItem.scss';

interface Props {
  title?: string;
  value?: any;
  borderTop?: boolean;
  levelOfCare?: boolean;
  borderRadius?: boolean;
}

const AppointmentDetailItem: React.FC<Props> = (props) => {
  const { title, value, borderTop, levelOfCare, borderRadius } = props;

  return (
    <div
      className="AppointmentDetailItem-wrapper"
      style={{
        borderTop: borderTop ? '1px solid var(--petrol-10)' : 0,
        borderRadius: borderRadius ? '0px 0px 16px 16px' : 0,
      }}
    >
      {!levelOfCare && <div className="AppointmentDetailItem-title">{title}</div>}

      <div
        className="AppointmentDetailItem-value"
        style={{ color: levelOfCare ? 'var(--nightblue-90)' : 'var(--petrol-100)' }}
      >
        {value}
      </div>
    </div>
  );
};

export default AppointmentDetailItem;
